html,
body {
    font-family: 'Roboto', sans-serif;
    height: 100%;
    margin: 0;
}

body {
    height: 100%;
    background-color: #1D0F2F;
}

#bg-image-container {
    height: 100%;
    overflow: hidden;
}

#bg-image {
    filter: blur(5rem);
    -webkit-filter: blur(5rem);
    transform: translate(-12%, -90%) rotate(158deg) scale(5)
}

main {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
}

#btn-section {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
    height: 100%;
}

.btn-container {
    width: 33%;
    text-align: center;
}

button {
    height: 100px;
    width: 100px;
    background-color: #1E1E1E;
    color: white;
    border: none;
    border-radius: 50%;
    box-shadow: 2px 2px rgba(219, 219, 219, 0.5);
    font-size: 1rem;
    box-shadow: -1px -1px 5px #4B5EFC, -2px -10px 5px #5140FE, 0 -10px 5px #AE1ECB, 3px 6px 5px #D523A3, 1px 1px 5px #FE277E;
    transition: all ease-in-out;
    outline: none;
}

button.sound-active {
    background-image: linear-gradient(#5140FE, #FE277E);
    box-shadow: none;
}

@media only screen and (max-width: 600px) {
    .btn-container {
        width: 50%;
    }
}